import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import { AdaptivityProvider, AppRoot } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';

const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedList, setList] = useState(null);
	const [user, setUser] = useState(null);
	const [isFetching, setFetching] = useState(null);

	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchList() {

			let user = await bridge.send("VKWebAppGetUserInfo");

			let account = await fetch(`https://clientlist.ru/api/users/${user.id}`);
  			account = await account.json();
  			setUser(account);

			let res = await fetch(`https://clientlist.ru/api/list`);
  			let list = await res.json();

  			list = list.map((i) => {
  				if (i.tags.includes('Крым') && (i.tags.length > 1)){
  					i.tags = i.tags.filter((i) => i !== 'Крым');
  				}
  				i.client.avatar = 'https://cdn4.iconfinder.com/data/icons/gradient-ui-1/512/message-512.png';
  				return i;
  			});
  			setList(list);
			setPopout(null);

		}

		setList([]);
		fetchList();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const refresh = async (e) => {
		setFetching(true);
		let res = await fetch(`https://clientlist.ru/api/list`);
  		let list = await res.json();
  		list = list.map((i) => {
			if (i.tags.includes('Крым') && (i.tags.length > 1)){
				i.tags = i.tags.filter((i) => i !== 'Крым');
			}
			i.client.avatar = 'https://cdn4.iconfinder.com/data/icons/gradient-ui-1/512/message-512.png';		
			return i;
		});
  		setList(list);
  		setFetching(false);
	};

	return (
		<AdaptivityProvider>
			<AppRoot>
				<View activePanel={activePanel} popout={popout}>
					<Home id='home' list={fetchedList} go={go} refresh={refresh} isFetching={isFetching} />
				</View>
			</AppRoot>
		</AdaptivityProvider>
	);
}

export default App;

